body {
  // background: #0f0e0f;
  background: #fff;
}

.preloader {
  background: url('./loader.gif');
  width: 260px;
  height: 225px;
  background-size: 400px;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto; 
  &__wrap {
    opacity: 1;
    transition: 0.5s;
    &.hide {
      opacity: 0;
    }
  }
  &__text {
    position: absolute;
    bottom: 19%;
    left: 0;
    right: 0;
    text-align: center;
    width: 100%;
    font-family: monospace;
  }
}

.video-react-playing{
  pointer-events: none !important;
}

.video-react-video {
  opacity: 0;
  transition: 0.5s;
}

.video-wrap{
  opacity: 0;
  transition: 0.5s;
  &.disable-pause{
    pointer-events: none;
  }
  &.show{
    opacity: 1;
  }
  &.playing{
    .video-react-video {
      opacity: 1;
    }
  }
}

.play-btn{
  opacity: 0;
  transition: 0.5s;
  position: fixed;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 1111;
  &-inner {
    position: absolute;
    width: 145px;
    height: 60px;
    background: red;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    z-index: 11111111;
    cursor: pointer;
    font-family: monospace;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border-radius: 10px;
    cursor: pointer;
    font-size: 18px;
  }
  &.show {
    opacity: 1;
  }
  &.hide{
    opacity: 0;
  }
}

.video-react-poster {
  display: none;
}

.video-react-big-play-button{
  display: none;
}

.video-react-control-bar{
  display: none;
}

.video-react {
  padding-top: 0 !important;
  video {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}